import "./App.css";
import Home from "./pages/Home";
import RarityP from "./pages/RarityP";
import UtilsP from "./pages/UtilsP";
import Error from "./pages/Error";

import {
  HashRouter,
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

function App() {
  return (
    <HashRouter>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/rarity" element={<RarityP />} />
        <Route path="/utilities" element={<UtilsP />} />
        <Route path="*" element={<Error />} />
      </Routes>
    </HashRouter>
  );
}

export default App;
