import React from "react";
import "./Roadmap.css";

import map from "../../Images/map.png";

const Roadmap = () => {
  return (
    <div className="RSuper" id="roadmap">
      <div className="RBox">
        <div className="RTitle">
          <p>Our roadmap = nonexistent</p>
          <h1>DuckMap</h1>
        </div>
        <div className="RContent">
          <div className="RImg">
            <img src={map} height="400" />
          </div>
          <div className="RText">
            <p>
              What the Duck no roadmap? we hear you ask, and its true (kind
              of).
              <br />
              As CC0 leaders in the NFT space, a roadmap simply doesnt cover
              it. We have so much in the works, including:
              <ul>
                <li>DCKS Gang: 10% of royalties back to the community ($$$)</li>
                <li>
                  DCKS Acadamy: Free NFT education to help the Duckies gang
                  learn and thrive
                </li>
                <li>
                  DCKS Ville: Discord group where Duckies meet and party all
                  night, every night
                </li>
              </ul>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Roadmap;
