import React from "react";
import "./About.css";

import img1 from "../../Images/apple.png";
import img2 from "../../Images/apple.png";
import img3 from "../../Images/apple.png";
import img4 from "../../Images/791.png";

const About = () => {
  return (
    <div className="ASuper" id="about">
      <div className="ABox">
        <div className="ATitleBox">
          <h1>Meet APT Ducks</h1>
        </div>
        <div className="ABoxContent">
          <img src={img4} height="100" />
          <p>
            APT Ducks is a collection of 3,333 NFTs jumping and hitting the
            APTOS blockchain. (Ever wonder where "do it like ducks" originated?
            It's from us, the APT Ducks family.) Each Duck features variations
            of traits, and they all have one thing in common: a love of parties
            and performing 24/7. Whether you want a cheesy character or one
            known for their crazy antics, we've got the perfect duck in mind.
            They may be playful, but our Ducks are helping us pave the way for
            the CC0 (aka open source branding) trend. We're talking about
            outreach projects, free NFT education, good vibes, and much more.
            Join DCKS Gang and help us bridge the gap between Web3 and the
            physical world. We care about the community here, and we can't wait
            to welcome our newest member: YOU!
          </p>
        </div>
        <div className="ADecoBox">
          <img src={img1} height="100" />
          <img src={img2} height="100" />
          <img src={img3} height="100" />
        </div>
      </div>
    </div>
  );
};

export default About;
