import React, { useState } from "react";
import "./Utils.css";

import working from "../../Images/workduck.png";
import btnImg1 from "../../Images/logo.png";

import duck from "../../Images/791.png";
import logoD from "../../Images/discord2.svg";
import logoT from "../../Images/twitter2.svg";
import logoG from "../../Images/gitbook.png";

import raidIco from "../../Images/raid.png";
import shopIco from "../../Images/shop.png";
import raffleIco from "../../Images/raffle.png";
import coinIco from "../../Images/coin.png";
import profileIco from "../../Images/profile.png";

import profileImg from "../../Images/Profile_Final_1.png";
import raffleImg from "../../Images/Raffle_1.png";
import stakingImg from "../../Images/Staking_1.png";

const Utils = () => {
  const [sneakImg, setSneakImg] = useState("raids");
  const [openSide, setOpenSide] = useState(true);
  const panelSwitch = () => {
    setOpenSide(!openSide);
  };
  return (
    <div className="USuper">
      {console.log(openSide)}
      <div className="USidePanel">
        <div className={openSide ? "UMainPanel" : "UMainAlt"}>
          <div className="UBtnContainer">
            <div
              className={sneakImg === "raids" ? "UPanelBtn UHL" : "UPanelBtn"}
              onClick={() => {
                setSneakImg("raids");
                setOpenSide(true);
              }}
            >
              <img src={raidIco} height="60" />
              {openSide ? <p>Raids</p> : <div></div>}
            </div>
            <div
              className={sneakImg === "shop" ? "UPanelBtn UHL" : "UPanelBtn"}
              onClick={() => {
                setSneakImg("shop");
                setOpenSide(true);
              }}
            >
              <img src={shopIco} height="60" />
              {openSide ? <p>Shop</p> : <div></div>}
            </div>
            <div
              className={sneakImg === "raffles" ? "UPanelBtn UHL" : "UPanelBtn"}
              onClick={() => {
                setSneakImg("raffles");
                setOpenSide(true);
              }}
            >
              <img src={raffleIco} height="60" />
              {openSide ? <p>Raffles</p> : <div></div>}
            </div>
            <div
              className={sneakImg === "stake" ? "UPanelBtn UHL" : "UPanelBtn"}
              onClick={() => {
                setSneakImg("stake");
                setOpenSide(true);
              }}
            >
              <img src={coinIco} height="60" />
              {openSide ? <p>Stake</p> : <div></div>}
            </div>
            <div
              className={sneakImg === "profile" ? "UPanelBtn UHL" : "UPanelBtn"}
              onClick={() => {
                setSneakImg("profile");
                setOpenSide(true);
              }}
            >
              <img src={profileIco} height="60" />
              {openSide ? <p>My Profile</p> : <div></div>}
            </div>
          </div>
          {openSide ? (
            <div className="UFSuper">
              <div className="UBox">
                <img src={duck} />
                <div className="USocials">
                  <a href="https://discord.gg/VfnABY5Muf" target="_blank">
                    <img src={logoD} />
                  </a>
                  <a href="https://twitter.com/APTDucks" target="_blank">
                    <img src={logoT} />
                  </a>
                  <a
                    href="https://whitepaper.aptducks.xyz/apt-ducks-gang/apt-ducks"
                    target="_blank"
                  >
                    <img src={logoG} />
                  </a>
                </div>
                <h1>2022  APT Ducks Productions, All Rights Reserved</h1>
              </div>
            </div>
          ) : (
            <div></div>
          )}
        </div>
        <div className="UDisplayPanel" onClick={() => panelSwitch()}>
          {openSide ? <h1>&#60;</h1> : <h1>&#62;</h1>}
        </div>
      </div>
      <div className="UContent">
        <img src={working} alt="" />
        <h2>Our Ducks are working on it...</h2>
        <div className="Sneakpeek">
          {sneakImg === "raids" && <></>}
          {sneakImg === "shop" && <></>}
          {sneakImg === "raffles" && (
            <>
              <h1>...But here is a sneak peek of what's to come!</h1>
              <img src={raffleImg} alt="" />
            </>
          )}
          {sneakImg === "stake" && (
            <>
              <h1>...But here is a sneak peek of what's to come!</h1>
              <img src={stakingImg} alt="" />
            </>
          )}
          {sneakImg === "profile" && (
            <>
              <h1>...But here is a sneak peek of what's to come!</h1>
              <img src={profileImg} alt="" />
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Utils;
