import React, { useState } from "react";
import "../Navbar/Navbar.css";
import { Link, animateScroll as scroll } from "react-scroll";
import { useNavigate } from "react-router-dom";

import logo from "../../Images/logo.png";
import logoD from "../../Images/discord2.svg";
import logoT from "../../Images/twitter2.svg";
import logoG from "../../Images/gitbook.png";
import hamico from "../../Images/hamico.png";
import closeico from "../../Images/close-button.png";

import btnImg1 from "../../Images/logo.png";

const Navbar = () => {
  const [isPanelOpen, setIsPanelOpen] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [menuImg, setMenuImg] = useState(hamico);

  let navigate = useNavigate();

  function openUtilities() {
    setIsPanelOpen(!isPanelOpen);
    setIsMenuOpen(false);
    setMenuImg(hamico);
  }

  function openMenu() {
    if (isMenuOpen) {
      setMenuImg(hamico);
    } else {
      setMenuImg(closeico);
    }

    setIsMenuOpen(!isMenuOpen);
    setIsPanelOpen(false);
  }

  function closeAll() {
    setIsMenuOpen(false);
    setIsPanelOpen(false);
    setMenuImg(hamico);
  }

  return (
    <div className="NavSuper" sticky="top">
      <div className="NavContainer">
        <div
          className="Navbox NavlogoAlt"
          onClick={() => {
            navigate("/");
          }}
        >
          <img src={logo} />
        </div>

        <div className="NavRightPanel">
          <div className="Navbox Navegacion">
            <h1
              onClick={() => {
                navigate("/");
              }}
            >
              HOME
            </h1>
          </div>
          <div className="Navbox Desplegable">
            <button
              className="MenuBtn"
              onClick={() => {
                navigate("/rarity");
              }}
            >
              RARITY
            </button>
          </div>
          <div className="Navbox Social">
            <a href="https://discord.gg/VfnABY5Muf" target="_blank">
              <img src={logoD} height="40" />
            </a>
            <a href="https://twitter.com/APTDucks" target="_blank">
              <img src={logoT} height="40" />
            </a>
            <a
              href="https://whitepaper.aptducks.xyz/apt-ducks-gang/apt-ducks"
              target="_blank"
            >
              <img src={logoG} height="40" />
            </a>
          </div>
          <div className="Navbox Navmint">
            <a href="https://discord.gg/VfnABY5Muf" target="_blank">
              <button className="NavbtnCTA">Join Us!</button>
            </a>
          </div>
          <div className="NSideMenu">
            <button className="sideMenuBtn" onClick={() => openMenu()}>
              <img src={menuImg} />
            </button>
          </div>
        </div>
      </div>
      <div className="NPaneles">
        <div className={"DDPanel-" + (isPanelOpen ? "open" : "closed")}>
          <div className="InnerPanel">
            <button className="PanelBtn">
              <img src={btnImg1} height="60" />
              <h1>&nbsp;&nbsp;Raids</h1>
            </button>
            <button className="PanelBtn">
              <img src={btnImg1} height="60" />
              <h1>&nbsp;&nbsp;Shop</h1>
            </button>
            <button className="PanelBtn">
              <img src={btnImg1} height="60" />
              <h1>&nbsp;&nbsp;Raffles</h1>
            </button>
            <button className="PanelBtn">
              <img src={btnImg1} height="60" />
              <h1>&nbsp;&nbsp;Stake</h1>
            </button>
          </div>
        </div>
        <div className={"SidePanel-" + (isMenuOpen ? "open" : "closed")}>
          <div className="InnerPanelSide">
            <div className="NaviSide">
              <h1
                onClick={() => {
                  navigate("/");
                }}
              >
                HOME
              </h1>
            </div>
            <div className="SocialSide">
              <a href="https://discord.gg/VfnABY5Muf" target="_blank">
                <img src={logoD} height="40" onClick={() => closeAll()} />
              </a>
              <a href="https://twitter.com/APTDucks" target="_blank">
                <img src={logoT} height="40" onClick={() => closeAll()} />
              </a>
              <a
                href="https://whitepaper.aptducks.xyz/apt-ducks-gang/apt-ducks"
                target="_blank"
              >
                <img src={logoG} height="40" onClick={() => closeAll()} />
              </a>
            </div>
            <div className="MintSide">
              <a href="https://discord.gg/VfnABY5Muf" target="_blank">
                <button className="SidebtnCTA" onClick={() => closeAll()}>
                  Join Us!
                </button>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
