import React, { useState } from "react";
import "./Rarity.css";
import { useNavigate } from "react-router-dom";

import db from "./db.json";

const Rarity = () => {
  const navigate = useNavigate();
  const [mainFilter, setMainFilter] = useState(null);
  return (
    <div className="RRSuper">
      <div className="MainTitle">
        <h1>CLICK ON A TRAIT</h1>
        <p>
          to list them below and see their <span>rarity</span>!
        </p>
      </div>
      <div className="MainFilter">
        {db &&
          db.map((data) => {
            return (
              <div
                className={
                  data.title === mainFilter ? "RRFilterBtn HL" : "RRFilterBtn"
                }
                key={data.id}
                onClick={() =>
                  setMainFilter(data.title === mainFilter ? null : data.title)
                }
              >
                <p>{data.title}</p>
              </div>
            );
          })}
      </div>
      <div className="CardContainer">
        <div className={mainFilter != null ? "RRContainerCommon" : ""}>
          {db &&
            db.map((data) => {
              return data.title === mainFilter ? (
                <>
                  <div className="SectionTitle Com">
                    <h1>Common</h1>
                  </div>
                  <div className="Collections">
                    {data.cards &&
                      data.cards.map((card) => {
                        return card.rarity === "Common" ? (
                          <div className="Card" key={data.id}>
                            <img src={card.img} alt="" />
                            {/* {card.img} */}
                            <h1>{card.name}</h1>
                            <h1>{card.ocurrence}</h1>
                          </div>
                        ) : (
                          <div></div>
                        );
                      })}
                  </div>
                </>
              ) : (
                <div></div>
              );
            })}
        </div>
        <div className={mainFilter != null ? "RRContainerRare" : ""}>
          {db &&
            db.map((data) => {
              return data.title === mainFilter ? (
                <>
                  <div className="SectionTitle Rar">
                    <h1>Rare</h1>
                  </div>
                  <div className="Collections">
                    {data.cards &&
                      data.cards.map((card) => {
                        return card.rarity === "Rare" ? (
                          <div className="Card" key={data.id}>
                            <img src={card.img} alt="" />
                            {/* {card.img} */}
                            <h1>{card.name}</h1>
                            <h1>{card.ocurrence}</h1>
                          </div>
                        ) : (
                          <div></div>
                        );
                      })}
                  </div>
                </>
              ) : (
                <div></div>
              );
            })}
        </div>
        <div className={mainFilter != null ? "RRContainerEpic" : ""}>
          {db &&
            db.map((data) => {
              return data.title === mainFilter ? (
                <>
                  <div className="SectionTitle Epi">
                    <h1>Epic</h1>
                  </div>
                  <div className="Collections">
                    {data.cards &&
                      data.cards.map((card) => {
                        return card.rarity === "Epic" ? (
                          <div className="Card" key={data.id}>
                            <img src={card.img} alt="" />
                            {/* {card.img} */}
                            <h1>{card.name}</h1>
                            <h1>{card.ocurrence}</h1>
                          </div>
                        ) : (
                          <div></div>
                        );
                      })}
                  </div>
                </>
              ) : (
                <div></div>
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default Rarity;
